import styled from 'styled-components';
import { string, arrayOf, exact, bool } from 'prop-types';
import { BoxHeading } from '../../components';
import Item from './Item';

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 80px;
  ${props => props.styles}
`;

const Items = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: min-content;
  grid-gap: 80px 72px;
`;

const NumberItems = props => (
  <Container styles={props.styles} className="number-items">
    <BoxHeading title={props.title} content={props.content}>
      <Items first={props.first} last={props.last} className="items">
      <Items>
        {props.items.map(item => (
          <div key={item.number}>{item.title}</div>
        ))}
      </Items>
      </Items>
    </BoxHeading>
  </Container>
);

NumberItems.propTypes = {
  title: string,
  content: string,
  first: bool,
  last: bool,
  items: arrayOf(
    exact({
      image: string,
      date: string,
      title: string,
      content: string,
    }),
  ),
  styles: arrayOf(string),
};

export default NumberItems;
